const date = new Date()
const year = date.getFullYear()
const experiences = [
  { id: 1, description: 'Cration auto entreprise  Dveloppeur Web', date: `2019${year}`, label: 'Jol Lesenne', content: "Inscription  la chambre de commerce et de l'URSSAF  Cration du site Web avec React.js et du nom de domaine  Cration du Logo et carte de visite." },
  { id: 2, description: 'Administrateur Informatique  Bnvole', date: '09/2015  09/2017', label: 'Accord2mains', content: "Mise en place d'un serveur Web et d'un CMS pour la gestion des adhrents  Cration d'une page web  et du nom de domaine  Cration du Logo et divers Flayers publicitaires." }
]

function Content (props) {
    return props.experiences.map((value) =>
      <article key={value.id}>
          <p className='description'>{value.description}</p>
          <h5><span className='date'>{value.date}</span>  <span className='label'>{value.label}</span></h5>
          <p className='content'>{value.content}</p>
      </article>
  )
}

export default function  Experiences() {
    return (
        <section className='section'>
            <h2>Expriences</h2>
            <hr />
            <Content experiences={experiences} />
        </section>
    )
}
